import axios from 'axios'

const FaqModules = {
    state:{
        FAQdata: []
    },
    mutations:{
        setData(state, payload){
            state.FAQdata = payload
        }
    },
    getters:{
        getAllData(state){
            return state.FAQdata
        }
    },
    actions:{
        fetchData(context){
            axios
            .get('data/FAQ.json')
            .then(res => context.commit("setData", res.data))
        }
    }
}

export default FaqModules