import axios from "axios";

const PresaleModules = {
	namespaced: true,
	actions: {
		fetchSelect(context){
			axios
			.get('data/select.json')
			.then(res => {
				function SortArray(x, y){
					if (x.country < y.country) {return -1;}
					if (x.country > y.country) {return 1;}
					return 0;
				}
				const selectData = res.data.sort(SortArray)
				context.commit("setSelectData", selectData)
			})
		},
		setLocalStorage(context){
			const userData = JSON.parse(localStorage.getItem('UserData'))
			context.commit("updateUserData", userData)
		}
	},
	mutations: {
		setSelectData(state, selectData){
			state.selectData = selectData
		},
		updateCountry(state, Country){
			state.country = Country
		},
		updateUserData(state, userData){
			if(localStorage.length !== 0){
				state.userData = userData
				if(userData.length !== 0){
					state.email = userData[0]
					state.amount= userData[1]
					state.bep20 = userData[2]
					state.twitterID = userData[3]
					state.telegramID = userData[5]
					state.country = userData[4]
					state.urlTweet = userData[6]
					state.urlLinked = userData[7]
				}
			}
		}
	},
	state: {
		selectData: [],
		userData: [],
		email: '',
		amount: '',
		twitterID: '',
		telegramID: '',
		country: '',
		bep20: '',
		urlTweet: '',
		urlLinked: ''
	},
	getters:{
        getSelectData(state){
            return state.selectData
        },
		getCountry(state){
            return state.country
        }
    },
}

export default PresaleModules